@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
	background-image: url(../img/header.jpg);
	background-size: cover;
	background-position: center;
	padding: 3rem 0 20rem 0;

	&__search {
		margin-top: 5rem;
		text-align: center;
		@include m.tablet {
			margin-top: 30rem;
		}
	}

	&__search-button {
		@include m.button(v.$white, v.$primary);
	}
}
