@use 'variables' as v;

html {
	font-size: 62.5%;
	box-sizing: border-box;
}
*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	font-family: v.$main_font;
	font-size: 1.6rem;
}

p {
	color: v.$black;
	font-size: 2rem;
}
.container {
	width: 95%;
	max-width: 1200px;
	margin: 0 auto;
}
img {
	max-width: 100%;
	display: block;
}

ul {
	padding: 0;
	list-style: none;
	margin: 0;
}

.section {
	padding: 2rem 0;
}
